/* Preventing the huge video from bleeding to other sections of the homepage */
@media screen and (min-width: 1200px) {
  section#hero-video {
    display: block;
    position: relative;
    height: 100vh;
    overflow: hidden;
    min-height: 450px;
  }
}

@media screen and (min-width: 1200px) {
  .video-background,
  .video-background iframe{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
}

@media screen and (min-width: 1200px) and (min-aspect-ratio: 16/9) {
  .video-background { height: 300%; top: -100%; }
}

@media screen and (min-width: 1200px) and (max-aspect-ratio: 16/9) {
  .video-background { width: 300%; left: -100%; }
}

@media screen and (max-width: 1199px) {
  .video-background {
    display: none;
  }

  #vidtop-content {
    position: relative;
    height: calc(100vh - 71px);
    background-image: url(../images/electron-video2.gif);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    min-height: 550px;
  }
}

