}

.vid-info {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
}

/* Retina */
/* fixing a bug with iphone and ipads */
@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
only screen and (-o-min-device-pixel-ratio: 3/2),
only screen and (min--moz-device-pixel-ratio: 1.5),
only screen and (min-device-pixel-ratio: 1.5) {
  .vid-info {
    top: 50%;
  }
}

/* fix titles on iphone5 landscape */
@media screen and (max-height: 320px) {
  .vid-info {
    position: static;
    transform: translate(0,0);
  }

  .scroll-down-message {
    position: static;
  }
}

.vid-info h1 {
    font-size: 33px;
    font-weight: normal;
    margin-top: 0;
    margin-bottom: 20px;
}

.paragraph--type--para-text {
  position: relative;
  top: -10px;
}

.vid-info p {
    margin-bottom: 3px;
}

.vid-info a { display: block; color: #fff; text-decoration: none; background: rgba(0,0,0,0.5); transition: .6s background; border-bottom: none; margin: 1rem auto; text-align: center; }

.scroll-down-message img {
  filter: invert(100%);
  width: 35px;
  margin-bottom: 15px;
}

.scroll-down-message {
  position: absolute;
  z-index: 500;
  color: white;
  left: 50%;
  text-align: center;
  font-size: 13px;
  transform: translate(-50% , 0);
  cursor: pointer;
  transition: all 0.2s;
  bottom: 10%;
}

.scroll-down-message:hover {
  bottom: 9%;
}


@media screen and (max-width: 1199px) {
  .scroll-down-message {
    bottom: 2%;
  }

  .scroll-down-message:hover {
    bottom: 1%;
  }
}
